import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import styled from 'styled-components';
import "./Detail.scss";

let 박스 = styled.div`
    padding: 20px;

`;

let 제목 = styled.h4`
    font-size: 25px;
    color : ${ props => props.색상 }
`;

// class Detail2 extends React.Component {
//   componentDidMount() {
//     //Detail2 컴포넌트가 보일때(렌더링이 될때) 실행할 코드
//     //Ajax같은 곳도 이런 곳에 자주 사용
//   }

//   componentWillUnmount() {

//   }

// }

function Detail(props) {
    let [alert, alert변경] = useState(true);
    let [inputData, inputData변경] = useState('');

    useEffect(() => {

      //컴포넌트가 mount 되었을 때 컴포넌트가 update될 때 특정코드가 실행됨
      //2초 후에 alert 창이 안보이게 해주자
      let 타이머 = setTimeout(() => {
        alert변경(false)
      }, 2000);
      console.log('안녕');
      return () => { clearTimeout(타이머) } //detail 컴포넌트가 사라질 때 타이머를 제거해줌.
    }, [alert]); //[]를 넣으면 Detail 등장시 한번 실행하고 끝남 / [alert]같은 특정 state가 변경될 때만 실행해주세요

    let { id } = useParams();
    let history = useHistory(); //방문기록 등을 담아두는 object

    let 찾은상품 = props.shoes.find(function(상품){
        return 상품.id == id
    });

    return(
      <div className="container">
        <박스>
            <제목 className="red">Detail</제목>
        </박스>

        {inputData}
        <input onChange={(e) => { inputData변경(e.target.value)}}/>

        { 
          alert === true 
          ? (<div className="my-alert2">
              <p>재고가 얼마 남지 않았습니다!</p>
            </div>)
          : null
        }

        <div className="row">
          <div className="col-md-6">
            <img src={'https://codingapple1.github.io/shop/shoes' + (찾은상품.id + 1) + '.jpg'} width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{찾은상품.title}</h4>
            <p>{찾은상품.content}</p>
            <p>{찾은상품.price}원</p>

            <Info 재고={props.재고}></Info>

            <button className="btn btn-danger" onClick={()=> { props.재고변경([9,11,12]) }}>주문하기</button> 
            &nbsp;
            <button className="btn btn-danger" onClick={() => { history.push("/") }}>뒤로가기</button> 
          </div>
        </div>
      </div>
    );
}

function Info(props) {
  return (
    <p>재고 : {props.재고[0]}</p>
  )
}

export default Detail;